import { getLyric } from 'api/song'
// import { getUid } from './uid'
import { Base64 } from 'js-base64'
import { ERR_OK } from 'api/config'

/* idurl */
let urlMap = {}

export default class Song {
  constructor ({ id, name, singer, mid, album, duration, image }) {
    this.id = id
    this.name = name
    this.singer = singer
    this.mid = mid
    this.album = album
    this.duration = duration
    this.image = image
    // filename
    this.filename = `C400${this.mid}.m4a`
    /* idurlidurl */
    if (urlMap[this.id]) {
      this.url = urlMap[this.id]
    } else {
      this._genUrl()
    }
  }
  /* midfilenameurl */
  _genUrl () {
    if (this.url) {
      return
    }
    this.url = 'http://aqqmusic.tc.qq.com/amobile.music.tc.qq.com/C4000029Lt3K2XVP75.m4a?guid=3383665865&vkey=3E5625436969E8B063637754A2DAA1C05A66A3D8FBEB15F033ED3DB3A8F9FE89542C30A9E96DAB5B930E1930DD780BE066639D9A5EE1FAE6&uin=0&fromtag=38'
    // getVKey(this.mid, this.filename).then((res) => {
    //   if (res.code === ERR_OK) {
    //     const vkey = res.data.items[0].vkey
    //     this.url = `http://dl.stream.qqmusic.qq.com/${this.filename}?vkey=${vkey}&guid=${getUid()}&uin=0&fromtag=66`
    //     urlMap[this.id] = this.url
    //   }
    // })
  }
  getLyric () {
    /* storecurrentSong
        */
    if (this.lyric) {
      return Promise.resolve(this.lyric)
    }
    /*  */
    return new Promise((resolve, reject) => {
      getLyric(this.mid).then((res) => {
        if (res.retcode === ERR_OK) {
          this.lyric = Base64.decode(res.lyric)
          resolve(this.lyric)
        } else {
          reject(new Error('no lyric'))
        }
      })
    })
  }
}

/*  */
export function createSong (musicData) {
  return new Song({
    id: musicData.songid,
    name: musicData.songname,
    singer: filterSinger(musicData.singer),
    mid: musicData.songmid,
    album: musicData.albumname,
    duration: 235 || musicData.interval,
    image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`
  })
}

/*  */
export function filterSinger (singer) {
  let ret = []
  /*  */
  if (!singer) {
    return ''
  }
  singer.forEach((item) => {
    ret.push(item.name)
  })
  return ret.join('/')
}
